.header {
  height: 70px;
  background-color: #FFC7C7;
}

.header__column {
  height: 100%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 500px) {
  .header__column {
    width: calc(100% - 12px);
  }
}

@media screen and (min-width: 500px) and (max-width: 1200px) {
  .header__column {
    width: calc(100% - 48px);
  }
}

@media screen and (min-width: 1200px) {
  .header__column {
    width: 1100px;
  } 
}

.header__logo {
  font-family: 'Marck Script', Inter, Arial, sans-serif;
	font-weight: 400;
	font-style: normal;
  margin: 0;
  color: black;
  text-decoration: none;
}

@media screen and (max-width: 1200px) {
  .header__logo {
    font-size: 23px;
  }
}

@media screen and (min-width: 1200px) {
  .header__logo {
    font-size: 42px;
  } 
}

.header__nav {
  display: flex;
  justify-content: space-between;
  width: 400px;
}

.header__nav-block {
  display: flex;
  justify-content: space-between;
}

.header__nav-link {
  font-family: 'Roboto Mono', Inter, Arial, sans-serif;
  font-weight: 300;
  font-size: 18px;
  color: rgb(59, 59, 59);
  text-decoration: none;
  transition: 0.2s;
  padding-bottom: 4px;
  box-sizing: border-box;
}

.header__nav-link:hover {
  color: black;
}

.header__add {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__add-info {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-right: 32px;
}

.header__add-info_aside-bar {
  margin: auto;
  margin-bottom: 100px;
}

.header__add-info-text {
  font-family: 'Roboto Mono', Inter, Arial, sans-serif;
  color: black;
  font-weight: 300;
}

@media screen and (max-width: 500px) {
  .header__add-info-text_time {
    font-size: 10px;
  }
}

@media screen and (min-width: 500px) {
  .header__add-info-text_time {
    font-size: 12px;
  }
}

.header__add-info-text_time_aside-bar {
  font-size: 16px;
}

.header__add-info-text_phone {
  transition: 0.2s;
  cursor: pointer;
}

@media screen and (max-width: 500px) {
  .header__add-info-text_phone {
    font-size: 10px;
  }
}

@media screen and (min-width: 500px) {
  .header__add-info-text_phone {
    font-size: 16px;
  }
}

.header__add-info-text_phone:hover {
  color: #8785A2;
}

.header__add-info-text_phone_aside-bar {
  font-size: 22px;
}

.header__phone {
  width: 30px;
  margin-right: 32px;
}

.header__button {
  width: 160px;
  height: 40px;
  border-radius: 10px;
  outline: none;
  border: none;
  cursor: pointer;
  background-color: black;
  color: white;
  margin-right: 32px;
}

.header__menu {
  width: 36px;
  height: 36px;
}

.header__aside-bar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 2;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  opacity: 1;
}

.nav-tab__aside-bar_inactive {
  display: none;  
}

.nav-tab__aside-bar-main {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 9999;
  background-color: grey;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  opacity: 0;
}

@media screen and (max-width: 740px){
  .nav-tab__aside-bar-main {
    width: 100%;
  }
}

@media screen and (min-width: 740px) and (max-width: 1262px){
  .nav-tab__aside-bar-main {
    width: 520px;
  } 
}

.nav-tab__aside-bar__column {
  display: grid;
  row-gap: 100px;
}

.nav-tab__aside-bar-reset {
  cursor: pointer;
  transition: 0.2s;
  justify-self: end;
}

@media screen and (max-width: 740px) {
  .nav-tab__aside-bar-reset {
    width: 30px;
    height: 30px;
    margin-top: 14px;
    margin-right: 14px;
  }
}

@media screen and (min-width: 740px) and (max-width: 1262px){
  .nav-tab__aside-bar-reset {
    width: 36px;
    height: 36px;
    margin-top: 30px;
    margin-right: 30px;
  } 
}

.nav-tab__aside-bar-reset:hover {
  opacity: 0.7;
}

.nav-tab__aside-bar-links {
  display: grid;
  justify-items: center;
  row-gap: 20px;
}

.nav-tab__aside-bar-link {
  color: black;
  text-decoration: none;
  transition: 0.2s;
  font-size: 18px;
  font-family: Inter, Arial, sans-serif;
  font-style: normal;
  font-weight: 500;  
}

@media screen and (max-width: 740px) {
  .nav-tab__aside-bar-link {
    line-height: 36px;
  }
}

@media screen and (min-width: 740px) and (max-width: 1262px){
  .nav-tab__aside-bar-link {
    line-height: 30px;
  } 
}

.nav-tab__aside-bar-link:hover {
  opacity: 0.7;
} 

.nav-tab__aside-bar-link_active {
  border-bottom: 2px solid black;
}