.main-1 {
  width: 100%;
  background-color: #FFE2E2;
}

.main-1__column {
  position: relative;
  height: 100%;
  margin: auto;
  padding: 80px 0;
}

@media screen and (max-width: 1200px) {
  .main-1__column {
    width: calc(100% - 48px);
  }
}

@media screen and (min-width: 1200px) {
  .main-1__column {
    width: 1100px;
  } 
}

.main-1__subscribe {
  font-family: 'Roboto Mono', Inter, Arial, sans-serif;
  font-weight: 100;
  border-bottom: 2px solid black;
  width: min-content;
  margin-bottom: 42px;
}

@media screen and (max-width: 1200px) {
  .main-1__subscribe {
    font-size: 36px;
  }
}

@media screen and (min-width: 1200px) {
  .main-1__subscribe {
    font-size: 54px;
  } 
}

.main-1__images {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 30px;
}

.main-1__images-column {
  display: grid;
  row-gap: 30px;
}

.main-1__images-column_1 {
  grid-template-rows: 2fr 3fr;
}

.main-1__images-column_2 {
  grid-template-rows: 3fr 2fr;
}

.main-1__image {
  width: 100%;
  height: 100%;
}

.main-2 {
  background-color: #F6F6F6;
  margin: auto;
}

.main-2__column {
  position: relative;
  height: 100%;
  margin: auto;
  padding: 80px 0 80px 0;
}

@media screen and (max-width: 600px) {
  .main-2__column {
    width: calc(100% - 48px);
  }
}


@media screen and (min-width: 600px) and (max-width: 1200px) {
  .main-2__column {
    width: calc(100% - 200px);
  }
}

@media screen and (min-width: 1200px) {
  .main-2__column {
    width: 1000px;
  } 
}

.main-2__title {
  font-family: Inter, Arial, sans-serif;
	font-weight: bold;

  margin: auto;
  margin-bottom: 48px;
}

@media screen and (max-width: 1200px) {
  .main-2__title {
    font-size: 30px;
    width: 342px;
  }
}

@media screen and (min-width: 1200px) {
  .main-2__title {
    font-size: 40px;
    width: 600px;
  } 
}

.main-2__info {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 60px;
  column-gap: 30px;
}

.main-2__subtitile {
  font-family: Inter, Arial, sans-serif;
	font-weight: bold;
  margin-bottom: 16px;
}

@media screen and (max-width: 1200px) {
  .main-2__subtitile {
    font-size: 18px;
  }
}

@media screen and (min-width: 1200px) {
  .main-2__subtitile {
    font-size: 24px;
  } 
}

.main-2__text {
  font-family: Inter, Arial, sans-serif;
  text-align: left;
}

@media screen and (max-width: 1200px) {
  .main-2__text {
    font-size: 12px;
  }
}

@media screen and (min-width: 1200px) {
  .main-2__text {
    font-size: 18px;
  } 
}
