.about-info {
  background-color: #F6F6F6;
  padding: 80px 0;
}

.about-info__column {
  margin: auto;

  display: grid;
  grid-template-columns: 2fr 1fr;
  column-gap: 32px;
}

@media screen and (max-width: 1200px) {
  .about-info__column {
    width: calc(100% - 48px);
  }
}

@media screen and (min-width: 1200px) {
  .about-info__column {
    width: 1100px;
  } 
}

.about-info__title {
  font-family: Inter, Arial, sans-serif;
  font-weight: normal;
  font-size: 32px;
  margin-bottom: 42px;
  text-align: left;
}

.about-info__text {
  font-family: Inter, Arial, sans-serif;
	font-weight: 400;
  margin-bottom: 42px;
  text-align: left;
}

.about-info__text_width {
  width: 60%;
}

.about-info__block {
  padding: 40px 60px;
  border-radius: 24px;
}

.about-info__block_1 {
  background-color: #8785A2;

  position: relative;
  overflow: hidden;
}

.about-info__curcle {
  position: absolute;
  width: 400px;
  bottom: -100px;
  right: -100px;
  animation: rotation 80s infinite;
}

@keyframes rotation {
  from {
      transform: rotate(0deg);
  }
  to {
      transform: rotate(360deg);
  }
} 

.about-info__block_2 {
  background-color: #FFE2E2;
}